import request from '@/utils/request'

export const getProduct = (obj) => {
  const { sortType, sortPrice, categoryId, goodsName, page } = obj
  return request({
    url: '/goods/list',
    params: {
      sortType,
      sortPrice,
      categoryId,
      goodsName,
      page
    }
  })
}
export const getProductDetail = (goodsId) => {
  return request({
    url: '/goods/detail',
    params: {
      goodsId
    }
  })
}
export const getProductComment = (goodsId, limit) => {
  return request({
    url: '/comment/listRows',
    params: {
      goodsId,
      limit
    }
  })
}
