<template>
    <div class="container">
      <van-nav-bar
        title="商品详情页"
        left-arrow
        @click-left="$router.go(-1)"
      />
      <van-swipe @change="onChange" autoplay="4000">
        <van-swipe-item  v-for="item in GoodsImage" :key="item.file_id">
          <img class="swipeImg" :src="item.external_url">
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }}/{{ GoodsImage.length }}</div>
        </template>
      </van-swipe>

      <div>
        <div class="shoppingInfo">
          <div class="price">
            <div class="newPrice">￥{{GoodsItem.goods_price_min}}</div>
            <div class="oldPrice">￥{{GoodsItem.goods_price_max}}</div>
          </div>
          <div class="sell">已售{{GoodsItem.goods_sales}}件</div>
        </div>
        <div class="shopName">{{GoodsItem.goods_name}}</div>

      <div class="AfSale">
        <div class="AfReturn">
          <van-icon name="passed" class="AfIcon" />
          <div>七天无理由退货</div>
        </div>
        <div class="AfDeliver">
          <van-icon name="passed" class="AfIcon" />
          <div>48小时内发货</div>
        </div>
        <div class="jt">></div>
        </div>
      </div>

      <div class="evaluateHead">
        <div class="evaluateLeft">商品评价({{total}}条)</div>
        <div class="evaluateRight"> 查看更多></div>
      </div>

        <div class="userEvaluate"  v-for="item in GoodsComment" :key="item.comment_id">
          <div class="userEvaluateHead">
            <img class="UserImg" :src="item.user.avatar_url || defaultImg">
              <div class="UserName">{{item.user.nick_name}}</div>
            <van-rate :value="item.score /2" readonly class="IconEvaluate"/>
          </div>
            <div class="Evaluate">
            {{item.content}}
            </div>
            <div class="EvaluateTime">
              <div class="year">{{item.create_time}}</div>
            </div>
          </div>
          <div class="desc" v-html="GoodsItem.content">
          </div>
          <!-- 商品底部 -->
      <div class="boxFoot">
        <div>
          <van-icon name="wap-home-o" class="homeIcon" @click="$router.push('/')" />
          <div class="home">首页</div>
        </div>
        <div>
          <van-icon name="cart-o" :badge="CartCount" class="cartIcon" @click="$router.push('/cart')"/>
          <div class="cart">购物车</div>
        </div>
        <button class="joinCart" @click="addFn">加入购物车</button>
        <button class="Buy" @click="BuyNow">立即购买</button>
      </div>

      <!-- 商品弹层 -->
      <van-action-sheet v-model="ActionShow" :title=" mode === 'cart' ? '购物车' : '立即购买'">
        <div class="content">
          <img :src='GoodsItem.goods_image' class="ActionImg">
          <div>
            <div class="newPrice">￥{{GoodsItem.goods_price_min}}</div>
            <div class="inventory">库存{{GoodsItem.stock_total}}</div>
          </div>
        </div>
        <div class="NumberBody">
          <div class="BuyNumber">数量</div>
          <div><CountBox v-model="addCount"></CountBox></div>
        </div>
        <div class="ButtonBody" v-if="GoodsItem.stock_total > 0">
          <button class="ActionButton" v-if="mode === 'cart'" @click="addCart">加入购物车</button>
          <button class="BuyButton" v-else @click="goBuy">立即购买</button>
        </div>
        <div  class="shoppingNone" v-else>
          <button class="ButtonNone">该商品已抢完</button>
        </div>
      </van-action-sheet>
    </div>
  </template>

<script>
import { getProductDetail, getProductComment } from '@/api/product'
import defaultImg from '@/assets/default-avatar.png'
import CountBox from '@/components/CountBox.vue'
import { addCart } from '@/api/cart'
export default {
  name: 'orderDetailIndex',
  data () {
    return {
      current: 0,
      value: 5,
      GoodsId: {},
      GoodsItem: [],
      GoodsImage: [],
      GoodsComment: [],
      total: '',
      defaultImg,
      ActionShow: false,
      mode: '',
      addCount: 1,
      CartCount: 0
    }
  },
  components: {
    CountBox
  },
  methods: {
    onChange (index) {
      this.current = index
    },
    async getProductDetail () {
      const res = await getProductDetail(this.getGoodId)
      this.GoodsItem = res.data.detail
      this.GoodsImage = res.data.detail.goods_images
    },
    async getProductComment () {
      const res = await getProductComment(this.getGoodId, 3)
      // console.log(res)
      this.GoodsComment = res.data.list
      console.log(this.GoodsComment)
      this.total = res.data.total
    },
    addFn () {
      this.ActionShow = true
      this.mode = 'cart'
    },
    BuyNow () {
      this.ActionShow = true
      this.mode = 'BuyNow'
    },
    async addCart () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要登录才能继续操作',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }

            })
          })
          .catch(() => {
          // on cancel
          })
        return
      }
      const res = await addCart(this.getGoodId, this.addCount, this.GoodsItem.skuList[0].goods_sku_id)
      console.log(res)
      this.CartCount = res.data.cartTotal
      this.$toast('加入购物车成功')
      this.ActionShow = false
    },
    goBuy () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要登录才能继续操作',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }

            })
          })
          .catch(() => {
          // on cancel
          })
        return
      }
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'buyNow',
          goodsId: this.getGoodId,
          goodsSkuId: this.GoodsItem.skuList[0].goods_sku_id,
          goodsNum: this.addCount
        }
      })
    }
  },
  computed: {
    getGoodId () {
      return this.$route.params.id
    }
  },
  created () {
    this.getProductDetail()
    this.getProductComment()
  }
}
</script>

  <style lang="less" scoped>
