    margin-left: 20px;
    font-size: 20px;
    color: red;
    font-weight: 600;
  }
  .oldPrice{
    margin-left: 5px;
    font-size: 15px;
    font-weight: 600;
    margin-top: 5px;
    color: gray;
    text-decoration: line-through;
  }
  .price{
    display: flex;
    float: left;
    margin:  0 auto;
    margin-left: -25px;
  }
  .sell{
    float: right;
    margin-top: 10px;
    font-size: 15px;
    color: gray;
  }
  .shopName{
    font-size: medium;
    font-weight: 500;
    margin-top: 10px;
  }
  .AfSale{
    display: flex;
    background-color: rgb(249, 249, 249);
    align-items: center;
    margin-top: 20px;
  }
  .AfReturn{
    display: flex;
    /* margin-top: 15px; */
    margin-left: 10px;
    justify-content: center;
  }
  .AfDeliver{
    display: flex;
    /* margin-top: 15px; */
    margin-left: 10px;
    justify-content: center;
  }
