    margin-top: 2px;
    color: green;
    font-weight: 600;
    margin-right: 3px;
  }
  .jt{
    margin-left: auto;
    /* margin-top: 13px; */
    font-size: 20px;
    font-weight: 500;
  }
  .boxFoot{
  background-color: white;
    width: 100%;
    display: flex;
    align-items: flex-end;
    position: fixed;
    margin-top: auto;
    bottom: 0;
  }
